#HDD, #SSD, #Multi, #Single {
  position: relative;
}

#HDD:checked:after, #SSD:checked:after, #Multi:checked:after, #Single:checked:after {
  content: "";
  width: 8px;
  height: 8px;
  background-color: #db678f;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

